@media screen and (max-width: 500px){
    .wallet-form-container{
        max-width: 100%;
        display: flex;
        flex-wrap: wrap;
        font-size: 16px;
        text-align: center;
    }
    
    .wallet-form-container .row{
        margin-bottom: 20px;
    }
    
    .wallet-form-container .row label{
        margin-left: 50px;
    }
    .wallet-form-container .row label:first-child{
        margin-left: 0;
    }
    .wallet-form-container .row .ReactSelect{
        margin-bottom: 10px;
    }
    .wallet-form-container input{
        font-size: 14px;
        padding: 5px 5px;
        margin-left: 25px;
    }
}
@media screen and (min-width: 500px) and (max-width: 900px){
    .wallet-form-container{
        max-width: 96%;
    }
    .wallet-form-container input{
        font-size: 14px;
        padding: 5px 5px;
        margin-left: 25px;
    }
}
@media screen and (min-width: 1000px) and (max-width: 1280px){
    .wallet-page-container{
        max-width: 90%;
    }
    .wallet-form-container input{
        font-size: 14px;
        padding: 5px 5px;
        margin-left: 25px;
    }
}