@media screen and (max-width: 500px){
    .container{
        max-width: 100%; 
        margin: auto;
        display: grid;
    }
    
    .container .ReactButton{
        margin: auto;
        margin-bottom: 50px;
    }
    .loading{
        vertical-align: middle;
        text-align: center;
    }
    .loading > img{
        width: 30vh;
    
    }
    
    .deposit-info{
        display: flex;
        flex-wrap: wrap;
        font-size: 16px;
        margin-bottom: 50px;
    }
    .deposit-info .column{
        margin-left: 0px;
        margin-top: 20px;
    }
    .deposit-info .column:first-child{
        margin-left: 0px;
        margin-top: 0px;
    }
}
@media screen and (min-width: 500px) and (max-width: 900px){
    .deposit-info input{
        font-size: 14px;
        padding: 5px 5px;
        margin-left: 25px;
    }
    .deposit-info select {margin-left: 25px;}
    .deposit-info .column{
        margin-left: 0px;
        margin-top: 20px;
    }
    .deposit-info .column:first-child{
        margin-left: 0px;
        margin-top: 0px;
    }
}
@media screen and (min-width: 1000px) and (max-width: 1280px){
    .deposit-page-container{
        max-width: 90%;
    }
    .deposit-form-container input{
        font-size: 14px;
        padding: 5px 5px;
        margin-left: 25px;
    }
}