@media screen and (max-width: 500px){
    
    .input-credit-form .input-credit-container{
        font-size: 16px;
        max-width: 100%;
        margin: auto;
    }
    .input-credit-form .input-credit-container .row{
        display: flex;
        justify-content: start;
        flex-wrap: wrap;
        align-items: center;
    }
    .input-credit-form .input-credit-container .row:first-child{
        margin-bottom: 20px;
    }
    .input-credit-form .input-credit-container .row .column{
        margin-top: 20px;
    }
    .input-credit-form .input-credit-container .row{
        margin-bottom: 40px;
    }
    .input-credit-form .input-credit-container .row .column:nth-child(2){
        margin-left: 0px;
    }
}
@media screen and (min-width: 500px) and (max-width: 900px){
    .input-credit-form .input-credit-container{
        font-size: 16px;
        max-width: 100%;
        margin: auto;
    }
    .input-credit-form .input-credit-container .row{
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .input-credit-form .input-credit-container .row:first-child{
        margin-top: 20px;
        margin-bottom: 20px;
    }
    .input-credit-form .input-credit-container .row{
        margin-bottom: 40px;
    }
    .input-credit-form .input-credit-container .row .column:nth-child(2){
        margin-left: 0px;
    }
}
@media screen and (min-width: 1000px) and (max-width: 1280px){
    .input-credit-form .input-credit-container{margin-bottom: 50px;}
}