@media screen and (max-width: 500px){
    .home-page-container{
        max-width: 96%;
    }
    .home-page-container h1{margin-bottom: 10px; font-size: 20px;}
    .home-page-container h3{font-size: 14px;}
    .slide-container button{
        display: none;
        opacity: 0;
    }
    .slide-container {
        text-align: center;
        padding: 0 10px 10px 10px;
    }
    .slider-control-bottomcenter svg{
        width: 10px;
    }
    .slide-container h2{font-size: 18px;}
    .slide-container h2, .slide-description{
        max-width: 90%;
        margin: auto;
        margin-bottom: 10px;
    }
    
    .slide-container .slide-description{
        margin-bottom: 20px;
    }
    .slide-container .slide-calc-description svg{
        width: 50px;
    }
    .slide-container .slide-calc-description h3{
        font-size: 14px;
    }
    .slide-container .slide-calc-description span{
        display: none;
    }
    .slide-container .slide-calc-description p{
        display: none;
    }
    .slide-container h3{
        margin-bottom: 10px;
    }
    .slide-container p{
        margin-top: 10px;
    }
    .slide-container span{
        margin-bottom: 10px;
    }
    .slide-description{margin-top: 20px;}
    .slide-calcs{
        display: grid;
        grid-template-columns: repeat(3,1fr);
    }
    
    .slide-calcs .slide-calc p{
        max-width: 10%;
    }
    .slide-calcs .slide-calc span{margin: 0;}
    
    .slide-container .slide-team {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        margin-top: 25px;
    }
    
    .slide-container .slide-team .slide-team-member svg{
        width: 50px;
        height: 50px;
    }
    .slide-container .slide-services{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: repeat(3, 1fr);
        margin-top: 10px;
    }
    .slide-container .slide-services .slide-service{margin-top: 20px;}
    .slide-container .slide-services .slide-service svg{
        height: 70px;
        margin-bottom: 10px;
    }
    .slide-container .slide-services .slide-service p{
        display: none;
    }
    .slide-container .slide-technologies svg{
        height: 75px; 
    }
    .slide-container .slide-technologies{
        display: flex;  
        justify-content: space-evenly;
        margin-top: 50px;
        flex-wrap:  wrap;
        align-content: space-evenly;
    }
    .slide-container .slide-technologies .l2{
        width: 100px;
       
    }

    .slide-container .slide-technologies .l3{
        width: 100px;
       
    }
    .slide-container .slide-technologies .l4{
        width: 100px;
        margin-top: 50px;
        
    }
    .slide-container .slide-technologies .l5{
        width: 100px;
        margin-top: 50px;
        
    }
    .slide-container .slide-technologies .l6{
        display: none;
    }
}
